<template>
    <div class="container content-container as-table__data company-page">
        <div class="toolsbar">
            <el-row class="btnBox">
                <el-button
                        size="mini"
                        type="primary"
                        icon="el-icon-plus"
                        @click="handleAdd">
                </el-button>
                <el-button
                        size="mini"
                        type="primary"
                        plain
                        icon="el-icon-edit"
                        @click="handleEdit">
                </el-button>
                <el-button
                        size="mini"
                        type="danger"
                        icon="el-icon-delete"
                        @click="handleDeleteSeleted">
                </el-button>
                <el-button
                        size="mini"
                        icon="el-icon-sort"
                        @click="importData">
                </el-button>
                <el-button
                        size="mini"
                        icon="el-icon-download"
                        @click="downloadTmp">
                </el-button>
                <el-button
                        size="mini"
                        icon="el-icon-download"
                        @click="exportCurrentData">
                </el-button>
                <el-button
                        size="mini"
                        icon="el-icon-download"
                        @click="exportAllData">
                </el-button>
            </el-row>
            <el-row class="searchBox">
                <el-form :inline="true" size="mini">
                    <el-form-item label="">
                        <el-input v-model="query.name" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="query.leader" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="query.utype_id" multiple filterable placeholder="" value-key="item">
                            <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-button type="primary" plain size="mini" @click="search" icon="el-icon-search"></el-button>
                    <el-button native-type="reset" @click="reset"></el-button>
                </el-form>
            </el-row>
        </div>
        <div>
            <el-table
                    class="as-table"
                    :data="tableData"
                    border
                    stripe
                    size="mini"
                    :highlight-current-row="true"
                    :default-sort="{prop: 'id', order: 'ascending'}"
                    v-loading="loading"
                    @selection-change="handleSelectionChange"
                    ref="table"
                    @row-click="cellClick"
                    @row-dblclick="dblclick"
            >
                <el-table-column
                        type="selection"
                        width="30"
                        fixed
                >
                </el-table-column>
                <el-table-column
                        prop="id"
                        width="60"
                        fixed
                        sortable
                        label="#"
                >
                    <template slot-scope="scope">
                        <span>{{ scope.row.id }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        align="left"
                        label=""
                        width="150"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.unit_no }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        align="left"
                        label=""
                        width="200"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.name }}</span>
                        </div>
                    </template>
                </el-table-column>

                <el-table-column
                        align="left"
                        prop="utypes"
                        label=""
                        width="100"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ implode(scope.row.utypes, 'name').join(',') }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{scope.row.parent_id}}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        align="left"
                        label=""
                        width="250"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class=" name-wrapper">
                            <span class="text-els">{{ decodeAddress(scope.row.province, scope.row.city, scope.row.county)+(scope.row.detail ? scope.row.detail : '') }}</span>
                        </div>
                    </template>
                </el-table-column>

                <el-table-column
                        label=""
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.leader }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        width="100"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.leader_tel }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.concact_person }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        width="100"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.concact_tel }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        width="140"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.leader_tel }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        width="100"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.company_site }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.fax }}</span>
                        </div>
                    </template>
                </el-table-column>


                <el-table-column
                        label=""
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.region }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.grade }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        align="left"
                        label=""
                        width="150"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.qualification_no }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        width="110"
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.safety_permit }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.main_business }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ scope.row.remark }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                >
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <span>{{ status[scope.row.status] }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        width="165"
                        sortable
                        prop="updated_at"
                >
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i> <span style="margin-left: 10px">{{ scope.row.updated_at }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        width="165"
                        sortable
                        prop="created_at"
                        fixed="right"
                >
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i> <span style="margin-left: 10px">{{ scope.row.created_at }}</span>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <!---->
        <el-row class="paginate">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[30, 60, 90, 120]"
                    :page-size="30"
                    :pager-count="11"
                    layout="total, sizes, prev, pager, next, jumper,slot,->"
                    :total="total">
            </el-pagination>
        </el-row>
        <!--
        
        <el-dialog title="" :visible.sync="formShown" class="pro-add" v-dialogDrag :close-on-click-modal="false"  fullscreen="true" ref="dialog__wrapper" @dragDialog="handleDrag">
            <div class="line" v-dialogDragWidth="$refs.dialog__wrapper">
        -->
        <el-dialog :visible.sync="formShown" v-dialogDrag :close-on-click-modal="false" class="pro-add" @dragDialog="handleDrag">
            <div slot="title">
                <span class="el-dialog__title"></span>
                <button class="el-dialog_btn__fullscreen">
                </button>
            </div>
            <div class="line">
                <el-form class="clearfix" size="mini">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.name" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-select v-model="form.utype_id" placeholder="" multiple>
                            <el-option v-for="(item,index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-select v-model="form.parent_id" placeholder="" disabled>
                            <el-option v-for="(item,index) in units" :key="index" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                        <el-button plain @click="searchUnitBox">...</el-button>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-select v-model="form.unit_attr_id" placeholder="">
                            <el-option v-for="(item,index) in attrs" :key="index" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-select v-model="form.status" placeholder="">
                            <el-option v-for="(item,index) in status" :key="index" :label="item" :value="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-cascader v-model="form.address" :options="addressData"></el-cascader>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.detail"></el-input>
                    </el-form-item>

                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.unit_no"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.qualification_no"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.safety_permit"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.email"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.concact_person"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.concact_tel"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.leader"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.leader_tel"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.company_site"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.fax"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input type="textarea" v-model="form.main_business"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input type="textarea" v-model="form.remark"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="formShown = false"> </el-button>
                <el-button type="primary" @click="onSubmit"></el-button>
            </div>
        </el-dialog>


        <el-dialog title="EXCEL" :visible.sync="excelDialogShow" v-dialogDrag :close-on-click-modal="false" class="pro-add">
            <div class="app-container">
                <upload-excel-component :on-success='handleSuccess' :before-upload="beforeUpload"></upload-excel-component>
                <el-table :data="excelData" border highlight-current-row style="width: 100%;margin-top:20px;">
                    <el-table-column v-for='item of excelHeader' :prop="item" :label="item" :key='item'>
                    </el-table-column>
                </el-table>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="excelDialogShow = false"> </el-button>
                <el-button type="primary" @click="importExcel"></el-button>
            </div>
        </el-dialog>

        <search-box :chose="chose" v-on:dbClickSelection="getUnitValue" v-on:closeSearchBox="closeUnitValue"></search-box>
    </div>
</template>

<script>
    //import elDragDialog from '../../directive/el-dragDialog'
    import UploadExcelComponent from '../../components/UploadExcel/index.vue'
    import {
        getUtypes,
        getUnits,
        editUnit,
        findUnit,
        updateUnit,
        storeUnit,
        destroyUnit,
        exportSelection,
        importExcel
    } from '../../api/company'
    import {implode, decodeAddress, encodeAddress, formatJson} from '../../utils/common'
    import {citys} from '../../api/json'
    import {status, attrs, exportTemp, tHeader, filterVal} from '../../config/company'
    import {pagesize, perPagesize} from '../../config/common'
    import SearchBox from '../../components/SearchBox.vue'
    import {export_json_to_excel} from '../../vendor/Export2Excel'

    export default {
        name: 'company',
        //directives: { elDragDialog },
        data() {
            return {
                query: {
                    leader: '', name: '', utype_id: null
                },
                tableData: [],
                multipleSelection: [],
                options: [],
                units: [],
                leader: '',
                utype_id: '',
                name: '',
                addressData: [],
                editData: {},
                currentPage: 1, //
                pagesize: pagesize,
                perPagesize: perPagesize,
                total: null,
                loading: true,
                searchData: {},
                chose: false,
                form: {
                    name: '',
                    unit_attr_id: null,
                    parent_id: null,
                    status: 0,
                    address: [],
                    detail: '',
                    unit_no: '',
                    qualification_no: '',
                    safety_permit: '',
                    concact_person: '',
                    concact_tel: '',
                    leader: '',
                    leader_tel: '',
                    company_site: '',
                    fax: '',
                    main_business: '',
                    remark: ''
                },
                formShown: false,
                formLabelWidth: "120px",
                submitType: '',
                filename: '',
                autoWidth: true,

                //excel
                excelData: [],
                excelHeader: [],
                excelDialogShow: false,
                finalExcelData: [],
                importExcelDataSwitch: false
            }
        },
        created() {
            this.$router.replace({path: this.$route.path, query: {page: this.currentPage}})

            citys().then(res => {
                this.addressData = res.data
            })
            getUtypes().then(res => {
                if (res.data.response_status === "success") {
                    this.options = res.data.data
                }
            })

            this.getTableData()

        },
        methods: {
            handleDrag() {
                //
                //this.$refs.select
            },
            // filterHandler(value, row, column) {
            //     const property = column['property'];
            //     return row[property] === value;
            //
            // },
            handleAdd() {
                this.form = {
                    name: '',
                    unit_attr_id: null,
                    parent_id: null,
                    status: 0,
                    address: [],
                    detail: '',
                    unit_no: '',
                    qualification_no: '',
                    safety_permit: '',
                    concact_person: '',
                    concact_tel: '',
                    leader: '',
                    leader_tel: '',
                    company_site: '',
                    fax: '',
                    main_business: '',
                    remark: '',
                    utype_id: []
                }
                this.formShown = true
                this.submitType = 'add'
            },
            handleSelectionChange(selection) {
                this.multipleSelection = implode(selection, 'id')
            },
            handleEdit() {
                this.submitType = 'edit'
                // 1
                if (this.multipleSelection.length === 1) {
                    editUnit(this.multipleSelection[0]).then(res => {
                        this.editData = res.data.data
                        // form
                        this.form = this.editData
                        this.$set(this.form, 'address', [this.editData.province, this.editData.city, this.editData.county])
                        this.$set(this.form, 'utype_id', implode(this.editData.utypes, 'id'))
                        // dialog
                        this.formShown = true

                        findUnit(this.editData.id).then(res => {
                            this.units.push(res.data.data)
                        })
                    })
                }
            },
            handleDeleteSeleted() {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    center: true
                }).then(() => {
                    destroyUnit({id: this.multipleSelection}, this.currentPage, this.pagesize).then(res => {
                        if (res.data.response_status === 'success') {
                            this.tableData = res.data.data.data
                            this.$message({
                                type: 'success',
                                showClose: true,
                                message: res.data.msg
                            })
                        }
                    })
                }).catch(() => {
                    return
                })
            },
            handleSizeChange(pagesize) {
                this.pagesize = pagesize
                this.getTableData(this.searchData)
            },
            handleCurrentChange(currentPage) {
                this.currentPage = currentPage
                this.$router.replace({path: this.$route.path, query: {page: this.currentPage}})
                this.getTableData(this.searchData)
            },
            onSubmit() {
                // 
                let data = this.form
                data.province = this.form.address[0]
                data.city = this.form.address[1]
                data.county = this.form.address[2]

                if (this.submitType === 'edit') {
                    updateUnit(this.editData.id, data).then(res => {
                        this.tableData.forEach((item, index) => {
                            if (item.id === this.editData.id) {
                                this.$set(this.tableData, index, res.data.data)
                                this.formShown = false
                                this.$message({
                                    type: 'success',
                                    showClose: true,
                                    message: res.data.msg
                                })
                            }
                        })
                    })
                } else {
                    storeUnit(data, this.pagesize).then(res => {
                        if (res.data.response_status === 'success') {
                            this.tableData = res.data.data.data
                            this.formShown = false
                            this.$message({
                                type: 'success',
                                showClose: true,
                                message: res.data.msg
                            })
                        }
                    })
                }
            },
            search() {
                this.getTableData(this.query)
            },
            reset() {
                for (var item in this.query) {
                    this.$set(this.query, item, '')
                }
                this.getTableData(this.query)
            },
            cellClick(row) {
                // this.$refs.table.toggleRowSelection(row)
                // this.$refs.table.clearSelection()
                this.$refs.table.toggleRowSelection(row, true)
            },
            dblclick(row) {
                this.submitType = 'edit'
                this.$refs.table.clearSelection()
                this.$refs.table.toggleRowSelection(row, true)
                findUnit(row.id).then(res => {
                    this.units.push(res.data.data)
                    editUnit(row.id).then(res => {
                        this.editData = res.data.data
                        // form
                        this.form = this.editData
                        this.$set(this.form, 'address', [this.editData.province, this.editData.city, this.editData.county])
                        this.$set(this.form, 'utype_id', implode(this.editData.utypes, 'id'))
                        // dialog
                        this.formShown = true
                    })
                })
            },
            implode(arr, attr) {
                return implode(arr, attr);
            },
            formatJson(filterVal, jsonData) {
                return formatJson(filterVal, jsonData)
            },
            decodeAddress(province_code, city_code, county_code) {
                let resultArr = []
                resultArr = decodeAddress(this.addressData, province_code, city_code, county_code)
                return resultArr.join('')
            },
            encodeAddress(province, city, county) {
                let resultArr = []
                resultArr = encodeAddress(this.addressData, province, city, county)
                return resultArr
            },
            getTableData(data = {}) {
                getUnits(this.currentPage, data, this.pagesize).then(res => {
                    if (res.data.response_status === "success") {
                        //
                        this.total = res.data.data.total
                        //table
                        this.tableData = res.data.data.data
                        // loading
                        this.loading = false
                    }
                })
            },
            searchUnitBox() {
                this.chose = true
            },

            getUnitValue(row) {
                this.chose = false
                this.$set(this.units, 0, {label: row.name, value: row.id})
                this.$set(this.form, 'parent_id', row.id)
            },
            importData() {
                this.excelDialogShow = true
            },
            downloadTmp() {
                const list = exportTemp
                const data = this.formatJson(filterVal, list)
                this.filename = 'company'
                export_json_to_excel({
                    header: tHeader,
                    data,
                    filename: this.filename,
                    autoWidth: this.autoWidth
                })

            },
            exportCurrentData() {
                if (this.multipleSelection.length > 0) {
                    exportSelection(this.multipleSelection).then(res => {
                        if (res.data.response_status === 'success') {
                            const list = this.formatUnitExportData(res.data.data)
                            const data = this.formatJson(filterVal, list)
                            this.filename = 'company-select'
                            export_json_to_excel({
                                header: tHeader,
                                data,
                                filename: this.filename + Math.random().toString(36).substr(2),
                                autoWidth: this.autoWidth
                            })
                        }
                    })
                }
            },
            exportAllData() {
            },
            closeUnitValue() {
                this.chose = false
            },
            formatUnitExportData(units) {
                const result = []
                units.forEach(item => {
                    result.push({
                        id: item.id,
                        name: item.name,
                        utypes: implode(item.utypes, 'name').join(','),
                        parent_id: item.parent.name,
                        unit_attr_id: attrs.filter(v => {
                            return v.value === item.unit_attr_id
                        })[0].label,
                        status: status[item.status],
                        address: this.decodeAddress(item.province, item.city, item.county) + (item.detail ? item.detail : ''),
                        unit_no: item.unit_no,
                        qualification_no: item.qualification_no,
                        safety_permit: item.safety_permit,
                        leader: item.leader,
                        leader_tel: item.leader_tel,
                        concact_person: item.concact_person,
                        concact_tel: item.concact_tel,
                        email: item.email,
                        company_site: item.company_site,
                        fax: item.fax,
                        main_business: item.main_business,
                        remark: item.remark,
                    })
                })
                return result
            },
            beforeUpload(file) {
                const isLt1M = file.size / 1024 / 1024 < 1

                if (isLt1M) {
                    return true
                }

                this.$message({
                    message: 'Please do not upload files larger than 1m in size.',
                    type: 'warning'
                })
                return false
            },
            handleSuccess({results, header}) {
                this.excelData = results
                this.excelHeader = header
            },
            importExcel() {
                this.finalExcelData = this.formatUnitimportData(this.excelData)
            },
            formatUnitimportData: function (units) {
                const result = []
                units.forEach((item, index) => {
                    const utypes = item[''].split(',')
                    const parent_unit = item['']
                    importExcel({utypes, parent_unit}).then(res => {
                        //excel
                        if (units.length - 1 === index) {
                            this.importExcelDataSwitch = true
                        }
                        const utype_id = res.data.data.utype_id
                        const parent_id = res.data.data.parent_id

                        const address = item[''].split(' ')
                        const addressCode = this.encodeAddress(address[0], address[1], address[2])
                        result.push({
                            name: item[''],
                            utype_id,
                            parent_id,
                            unit_attr_id: attrs.filter(v => {
                                return v.label === item['']
                            })[0].value,
                            status: status.indexOf(item['']),
                            province: addressCode[0] || '',
                            city: addressCode[1] || '',
                            county: addressCode[2] || '',
                            unit_no: item[''],
                            qualification_no: item[''],
                            safety_permit: item[''],
                            leader: item[''],
                            leader_tel: item[''],
                            concact_person: item[''],
                            concact_tel: item[''],
                            email: item[''],
                            company_site: item[''],
                            fax: item[''],
                            main_business: item[''],
                            remark: item[''],
                        })
                    })
                })
                return result
            },
        },
        computed: {
            status() {
                return status
            },
            attrs() {
                return attrs
            }
        },
        watch: {
            importExcelDataSwitch(val) {
                if (val) {
                    const finalExcelData = this.finalExcelData
                    const pagesize = this.pagesize
                    importExcel({finalExcelData, pagesize}).then(res => {
                        if (res.data.response_status === 'success') {
                            this.importExcelDataSwitch = false
                            //
                            this.total = res.data.data.total
                            //table
                            this.tableData = res.data.data.data
                            // loading
                            this.loading = false

                            this.excelData = []
                            this.$message({
                                type: 'success',
                                showClose: true,
                                message: ''
                            })
                            this.excelDialogShow = false
                        }
                    })
                }
            }
        },
        components: {
            SearchBox,
            UploadExcelComponent
        }
    }
</script>





